<template>
  <v-form ref="form" v-model="valid" @submit.prevent="onSubmit">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-text-field
            label="Name"
            placeholder="Person name"
            v-model="person.name"
            :rules="rules.name"
            autofocus
          />
          <v-text-field
            label="Strain length"
            placeholder="Strain length"
            type="number"
            v-model="person.strain_length"
            :rules="rules.strain_length"
          />
        </v-col>
        <v-col cols="6">
          <v-select
            :items="[{text: 'Left', value: 0}, {text: 'Right', value: 1}]"
            label="Hand"
            item-value="value"
            v-model="person.hand"
            :rules="rules.hand"
          ></v-select>
          <v-text-field label="City" placeholder="City" v-model="person.city" :rules="rules.city" />
        </v-col>
        <v-col cols="12">
          <v-textarea label="Comment" placeholder="Comment" v-model="person.comment"></v-textarea>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-btn type="submit" color="primary" class="white--text mr-4" :disabled="!valid">Save</v-btn>
          <v-btn class="mr-4" @click="onCancel">Cancel</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { required, isNumber } from "@/libs/validations";

export default {
  props: ["item"],
  mounted() {
    if (this.item) this.person = { ...this.item };
  },
  data() {
    return {
      valid: false,
      person: {
        name: "",
        strain_length: "",
        hand: null
      },
      rules: {
        name: [required],
        strain_length: [required, isNumber],
        city: [required]
      }
    };
  },
  methods: {
    async onSubmit() {
      this.$emit("onSubmit", { ...this.person });
      //this.person.name = "";
      this.resetForm();
    },

    async onCancel() {
      this.$emit("onCancel");
      this.resetForm();
    },

    resetForm() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>
